

import { Pipe, PipeTransform } from '@angular/core';
import { FOPModel } from 'src/models/fop/fop.model';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';

@Pipe({ name: 'fopFilterPipe' })
/**
 * Fop Filter
 */
export class FopFilterPipe implements PipeTransform {

    constructor(private u: CurrentUserService) { }

    transform(items: any[], searchText: string, fop: FOPModel): any {
        if (!items || !searchText) return items;
        return this.filterArray(items, searchText, fop);
    }

    /**
 *     
 * @param {any} items    
 * @param {any} searchText 
 * @param {any} fieldTargets     
 */
    filterArray(items: any[], searchText: string, fop: FOPModel) {
        let result = [],
            searchTextIsNotNumber = isNaN(Number(searchText));


        //Filter Now
        items.forEach(it => {
            let isAllow = false;
            for (const field of fop.fieldNames) {
                /**
                               *              "" 
                               *                                  , 
                               */
                let pattern = searchTextIsNotNumber ? `"${field}":".*${searchText}.*"` : `"${field}":.*${searchText}.*(,|\})`,
                    reg = RegExp(pattern, "mi");
                isAllow = reg.test(JSON.stringify(it));

                //Out Fro Loop If Current Object Is Allow 
                if (isAllow)
                    break;
            }
            if (isAllow)
                result.push(it);
        });

        //ReFop Now For Update pagination
        fop.reFop(result.length);

        return result;
    }

}//End Clss